.single-order{
  &__top{
    &-col{
      display: flex;
      align-items: flex-end;
    }
  }
  &__date{
    margin: 0 0 .33em .875em;
  }
  .order-table{
    margin-top: 1.5em;
  }
  .order-table-head__title{
    opacity: 0.8;
  }
  @media screen and (min-width: 993px){
    .order-table-head,
    .order-row{
      grid-template-columns: 4.375em 10em 20em 6em 4.625em 6.875em;
    }
  }
  @media screen and (min-width: 577px){
    .order-table-head2{
      .order-table-head__col{
        &:nth-child(4),
        &:nth-child(8){
          text-align: left;
        }
      }
    }
    .order-row2{
      .order-row__col{
        &:nth-child(4),
        &:nth-child(8){
          text-align: left;
        }
      }
    }
    .order-table-head2,
    .order-row2{
      grid-template-columns: 4em 4.375em 4em 6.25em 6em 4.625em 6.875em 6.75em 7.5em;
      .text14{
        text-align: left;
      }
    }
  }

}

.order-info2{
  margin-top: 2em;
  &--manager{
    border-top: $border2;
    padding-top: 1.25em;
    margin-top: 1.5em;
  }
  &~.load-more{
    margin-top: 3.5em;
  }
  &__props{
    margin-top: 1.5em;
    @include grid100gap(.6875em);
  }
  &__prop{
    display: grid;
    grid-template-columns: 12.5em calc(100% - 13em);
    gap: .5em;
    &-col2{
      display: flex;
      align-items: flex-start;
      flex-direction: column;
    }
    &-title{
      font-weight: 800;
      color: rgba($gray,.8);
    }
    &-btn{
      padding: 0 2em;
      margin-top: .5em;
    }
  }

}




@media screen and (max-width: 992px){
  .single-order{
    &__top{
      .order-card__badge{
        display: none;
      }
    }
    &__date{
      display: none;
    }
    @media screen and (min-width: 577px){
      .order-table-head2,
      .order-row2{
        font-size: .875em;
      }
    }
  }

}


@media screen and (max-width: 576px){
  .order-info2{
    &__props{
      padding-bottom: 3.1875em;
      position: relative;
    }
    &__prop{
      grid-template-columns: 8em calc(100% - 8.5em);
      &-btn{
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        margin: 0;
      }
      &-title{
        line-height: 133%;
      }
    }
  }
}
