.cart{
  padding: 0 0 3.125em;
  &__total{
    margin-top: 1em;
    display: flex;
    justify-content: flex-end;
    .text14{
      margin-left: .7em;
    }
  }
  &__bottom{
    margin-top: 4.2em;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__hint{
    margin-top: .625em;
    max-width: 21em;
    text-align: center;
  }
  &__detail{
    margin-top: .625em;
    padding: 2.5em 8.2em 1.5em 3.75em;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
  }
  &__all{
    @include grid100gap(.5em);
    min-width: 12em;
    margin-right: 1em;
  }
  &__prices{
    @include grid100gap(.25em);
  }
  &__price{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    .text14{
      padding-bottom: .125em;
      margin-right: .7em;
    }
    .text16{
      margin-left: .25em;
    }
  }
  &__action{
    margin-top: .625em;
    border-top: 1px solid $dividerGray;
    border-bottom: 1px solid $dividerGray;
    padding: 1em 0;
    @include center;
    &-btn{
      font-weight: 600;
      display: flex;
      align-items: center;
      &:hover{
        color: $primary;
        path{
          stroke: $primary;
        }
      }
      &:not(:last-child):after{
        content: '';
        width: .125em;
        height: 1em;
        flex-shrink: 0;
        margin: 0 .8em;
        background-color: #E2E2E2;
      }
      svg{
        width: 1.5em;
        height: auto;
        margin-right: .5em;
        flex-shrink: 0;
      }
      span{
        font-size: .875em;
      }
      ._view2{
        display: none;
      }
    }
  }
}

.cart-sales,
.cart-table{
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th{
    background-color: $bgGray;
    height: 2.75em;
    span{
      font-size: .75em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.05625em;
    }
  }
  th,
  td{
    padding-left: 1em;
    text-align: left;
    &:nth-child(1){
      padding-left: 1.25em;
      width: 2.75em;
    }
  }
  .m-check {
    input:checked + label:before {
      //background-color: $primary;
      //border-color: $primary;
    }

    label {
      padding-left: 1.5em;

      &:before {
        background-color: #fff;
      }

      &:after {
        //background-image: url(../img/check5.svg);
        background-size: 1em auto;
      }
    }
  }
}

.cart-table{
  margin-top: 1.25em;
  th,
  td{
    &:nth-child(2){
      width: 7.1875em;
    }
    &:nth-child(3){
      width: 4.5em;
    }
    &:nth-child(4){
      width: 4.5em;
    }
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4){
      text-align: center;
    }
    &:nth-child(6){
      width: 9.25em;
    }
    &:last-child{
      width: 4em;
    }
  }
  td{
    padding-top: 1.25em;
    padding-bottom: 1.25em;
    border-bottom: 1px solid $dividerGray;
  }
  tr:last-child{
    td{
      border-bottom: none;
    }
  }
  .m-check{
    input{
      &._halfChecked+label{
        &:before{
          background-color: $primary;
          border-color: $primary;
        }
        &:after{
          background-image: url(../img/minus.svg);
          opacity: 1;
        }
      }
    }
  }
  &__img{
    width: 7.1875em;
    height: 7.1875em;
    position: relative;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__sizes{
    @include grid100gap(.25em);
    @include sameNumWidth;
  }
  .cartcalc{
    width: 8.25em;
    height: 2.75em;
    grid-template-columns: 2.75em 2.75em 2.75em;
    input{
      font-size: 1.125em;
    }
    button{
      font-size: 1em;
      span{
        line-height: 120%;
        transform: translateY(-.05em);
      }
      svg{
        width: .875em;
      }
    }
  }
  &__delete{
    width: 4em;
    max-width: 100%;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__prices{
    @include grid100gap(.5em);
    white-space: nowrap;
  }
  &__price{
    white-space: nowrap;
  }
  &__title{
    td{
      padding: .625em 0 0 0 !important;
      border: none;
    }
  }
  .article-content__future{
    justify-content: unset;
    margin: 0;
  }
  tr._not-available{
    color: #B2B2B2;
    .primary-text{
      opacity: 0.8;
    }
    .cart-table__img{
      opacity: .5;
    }
  }
}

.cart-sales{
  margin-top: 2.125em;
  &__top{
    padding: 0 1.25em;
  }
  table{
    border-spacing: 0 .625em;
    border-collapse: unset;
  }
  th,td{
    &:nth-child(2){
      width: 30.375em;
    }
  }
}

.order{
  margin-top: 1em;
  padding: 1.25em;
  &__subtitle{
    padding: 1.25em 0;
    border-bottom: 1px solid $dividerGray;
    &._no-border{
      border: none;
    }
  }
  &__upload{
    @include grid100gap(.75em);
  }
  &__links{
    display: flex;
    flex-wrap: wrap;
  }
  .m-check{
    label{
      span{
        line-height: 143%;
      }
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
    max-width: 52.625em;
  }
  ._h80{
    textarea{
      height: 5em;
    }
  }
}

.file{
  display: flex;
  align-items: flex-start;
  padding: 1em;
  border-radius: .5em;
  border: 1px solid $dividerGray;
  .upload__icon{
    flex-shrink: 0;
    margin: 0 1em 0 0;
  }
  &__content{
    padding-right: .5em;
  }
  &__delete{
    margin-left: auto;
    width: 1.25em;
    flex-shrink: 0;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.cart-nav{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 1.25em 0;
  box-shadow: 0px -16px 16px 0px rgba(0, 0, 0, 0.10), 0px -8px 8px 0px rgba(0, 0, 0, 0.05);
  background-color: #fff;
  z-index: 20;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__action{
    margin-left: 1.875em;
    min-width: 16.75em;
    .nice-select{
      background-image: url(../img/select-arrow3.svg)!important;
    }
    .list{
      box-shadow: 0 -1em 1em 0 rgba(0,0,0,.1),0 .5em .5em 0 rgba(0,0,0,.05);
      top: unset;
      bottom: 100%;
      margin-bottom: .25em;
    }
  }
  &__btn{
    margin-left: .5em;
  }
}

.table-item{
  &__title{
    display: none;
  }
}

.cartcalc{
  position: relative;
  width: 4.375em;
  height: 1.5em;
  display: grid;
  grid-template-columns: 1.5em 1.375em 1.5em;
  border-radius: .25em;
  border: 1px solid $dividerGray;
  &._disabled{
    button{
      pointer-events: none;
      color: #B2B2B2;
      opacity: .7;
      path{
        fill: #B2B2B2;
      }
    }
  }
  input,
  button{
    width: 100%;
    height: 100%;
    border: none;
    font-size: .75em;
    line-height: 120%;
    svg{
      width: .75em;
      height: auto;
    }
  }
  .ccalc-plus{
    svg{
      transform: translateX(-.1em);
    }
  }
  button{
    @include center;
  }
  input{
    border-right: 1px solid $dividerGray;
    border-left: 1px solid $dividerGray;
    text-align: center;
    font-weight: 800;
    margin: 0;
    padding: 0;
  }
  .hint-tooltip{
    top: 100%;
    margin-top: .875em;
    width: 16.75em;
    right: -7em;
    &~.ccalc-plus{
      path{
        fill: #FAECD8;
      }
    }
  }
}

.hint-tooltip{
  position: absolute;
  border-radius: 0.25em;
  background: $white;
  box-shadow: 0px 8px 20px 0px rgba(0, 51, 153, 0.08), 0px -8px 20px 0px rgba(0, 51, 153, 0.08);
  padding: .5em;
  text-align: center;
  font-weight: 300;
  &:before{
    content: '';
    position: absolute;
    background-image: url(../img/triangle.svg);
    width: 2em;
    height: .9375em;
    @include bgImgSize(100%);
    top: -.5em;
    left: 50%;
    transform: translateX(-50%);
  }
}


@media screen and (max-width: 992px){
  .cart-table{
    table,tbody{
      display: block;
    }
    thead{
      display: none;
    }
    td{
      border: none;
      padding: 0 !important;
      width: 100% !important;
      display: block;
      grid-column: 1/-1;
      text-align: left !important;
      &:nth-child(1),
      &:nth-child(2),
      &:last-child{
        grid-row: 1/2;
        @include center;
      }
      &:nth-child(1),
      &:last-child{
        justify-content: unset;
        align-items: unset;
        align-self: flex-start;
      }
      &:nth-child(1){
        grid-column: 1/2;
      }
      &:nth-child(2){
        grid-column: 2/3;
        margin-top: 3em;
      }
      &:last-child{
        grid-column: 3/4;
      }
      &:nth-child(3),
      &:nth-child(4){
        position: absolute;
        left: 2.25em;
        max-width: calc(100% - 4.5em);
      }
      &:nth-child(3){
        top: 1.5em;
      }
      &:nth-child(4){
        top: 3em;
      }
    }
    tr{
      border-top: 1px solid $dividerGray;
      display: grid;
      grid-template-columns: 4em calc(100% - 10em) 4em;
      gap: 1em;
      align-items: center;
      padding: 1.25em 0;
      position: relative;
      &:first-child{
        border-top: none;
      }
    }
    &__sizes{
      display: flex;
      flex-wrap: wrap;
      gap: unset;
      .text14{
        margin-right: .25em;
      }
    }
    &__title{
      display: block !important;
      text-align: left;
      td{
        justify-content: unset !important;
      }
    }
    &__delete{
      transform: translate(1.25em,-1.25em);
    }
    &__prices{
      gap: 0;
    }
    &__prices2{
      display: flex;
      align-items: center;
      gap: .5em;
    }
    &__price{
      .text18{
        font-size: 1em;
      }
    }
    ._hide-mob{
      display: none;
    }
    @media screen and (min-width: 577px){
      .cart-table__img{
        font-size: 2em;
      }
    }
  }

  .cart-table2{
    td{
      &:nth-child(6){
        grid-column: 1/2;
        width: 9.25em !important;
        .table-item{
          flex-direction: column;
          &__title{
            margin-bottom: .7em;
          }
        }
      }
      &:nth-child(7){
        grid-column: 2/4;
        justify-content: flex-end;
        width: 10.25em !important;
        margin-left: auto;
        .table-item{
          flex-direction: column;
          &__title{
            margin-bottom: .7em;
          }
        }
        .cartcalc{
          width: 100%;
          grid-template-columns: 2.75em calc(100% - 5.5em) 2.75em;
        }
      }
      &:nth-child(8),
      &:nth-child(9),
      &:nth-child(10){
        .table-item{
          justify-content: space-between;
          align-items: center;
        }
        .cart-table__price{
          .text16{
            font-size: .75em;
          }
        }
      }
      &:nth-child(9){
        margin-top: -.875em;
      }
      &:nth-child(10){
        margin-top: -.375em;
      }
      &:nth-child(8),
      &:nth-child(9){
        .table-item__title{
          font-size: .75em;
        }
      }
    }
  }

  .table-item{
    display: flex;
    &__title{
      display: block;
      margin-right: .25em;
    }
  }

  .cart{
    &__total{
      justify-content: unset;
      .text14{
        margin: 0 .7em 0 0;
      }
    }
    &__hint{
      text-align: left;
    }
    &__detail{
      margin-top: 1.5em;
      @include grid100gap(0);
      padding: 0;
      .cart__price{
        flex-wrap: wrap;
        justify-content: unset;
        margin: 1em 0 0;
        ._full{
          width: 100%;
        }
        .text16{
          transform: translateY(.175em);
        }
      }
    }
    &__prices{
      gap: 0;
    }
    &__action{
      width: calc(100% + 2em);
      margin-left: -1em;
      justify-content: unset;
      padding: 1.125em 1em;
      &-btn{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .cart-sales{
    width: calc(100% + 2em);
    margin-left: -1em;
    margin-top: 1.5em;
    th{
      &:nth-child(2){
        span{
          margin-left: -2.67em;
          display: block;
        }
      }
    }
    th,
    td{
      &:nth-child(1){
        vertical-align: top;
      }
      &:nth-child(2){
        padding-left: .5em;
        width: 8em;
      }
    }
  }

  .order{
    padding: 1.5em 1em;
    width: calc(100% + 2em);
    margin-left: -1em;
    border-top: 1px solid $dividerGray;
    &__links{
      flex-direction: column;
      align-items: flex-start;
      .cart__action-btn{
        @include lastM(0 0 .75em);
        &:after{
          display: none;
        }
        span{
          line-height: 215%;
        }
      }
    }
  }

  .cartcalc{
    .hint-tooltip{
      right: -1em;
      &:before{
        left: unset;
        right: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__bottom{
      margin-top: 2em;
      .m-btn{
        width: 100%;
        span{
          font-size: 1.125em;
        }
      }
    }
  }

  .cart-nav{
    &__inner{
      display: grid;
      grid-template-columns: calc(100% - 6.625em) 6.125em;
      gap: .625em .5em;
      .text18{
        grid-column: 1/-1;
      }
    }
    &__action{
      margin: 0;
      min-width: 100%;
    }
    &__btn{
      margin: 0;
    }
    .fg,
    .nice-select{
      position: static;
    }
    .list{
      border-bottom: 1px solid $dividerGray;
      bottom: 98%;
    }
  }
}
