.cart{
  margin-top: 0;
}

.cart-empty{
  margin: 74px 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 400;
  &__title{
    font-size: 48px;
    line-height: 125%;
    letter-spacing: 0.9px;
  }
  &__desc{
    margin-top: 50px;
    min-height: 61px;
  }
  .btn-default{
    margin-top: 20px;
  }
}

.auth{
  text-align: center;
  width: 50em;
  max-width: 100%;
  margin: 2.625em auto 0;
  &__title{
    font-size: 2.1875em;
    line-height: 92%;
    font-weight: 700;
    color: $dark;
  }
}

.auth-form{
  margin-top: 1em;
  @include grid100gap(1.875em);
  .m-btn-red{
    height: 3.1875em;
    span{
      font-size: 1.125em;
      letter-spacing: .1em;
    }
  }
  .m-btn-red-outline{
    display: inline-flex;
    margin: -.375em auto 0;
  }
}

.cart-promo{
  display: flex;
  align-items: center;
  margin-right: auto;
  &__title{

  }
  .fg{
    width: 12.5em;
    height: 2.5em;
    margin: 0 1em;
    input{
      height: 100%;
      font-size: .875em;
      padding: 0 1em;
      border-radius: .3em;
    }
    &._success{
      input{
        background-image: url(../img/check2.svg);
        background-size: 1.43em auto;
        background-position: left 1em center;
        background-repeat: no-repeat;
        padding-left: 3em;
      }
    }
  }
  &__btn{
    svg{
      display: none;
    }
  }
}

.cart-bottom{
  display: flex;
  justify-content: center;
  margin-top: 1.25em;
}

.warning-message{
  padding: .625em 1.25em;
  border-radius: 0.5em;
  border: 1px solid $primary;
  background: rgba(238, 28, 37, 0.10);
  margin-top: .625em;
  display: flex;
  align-items: center;
  min-height: 2.9375em;
}


@media screen and (max-width: 992px){
  .cart-empty{
    &__title{
      font-size: 34px;
    }
  }

  .auth{
    margin-top: .625em;
    &__title{
      font-size: 1.5em;
      line-height: 129%;
      text-align: left;
      width: 60%;
    }
  }

  .auth-form{
    .m-btn-red{
      margin-top: .75em;
    }
    .m-btn-red-outline{
      display: flex;
      width: 100%;
      margin-top: -1.125em;
    }
  }

  .cart-promo{
    margin: 0 0 1.25em;
  }
}


@media screen and (max-width: 576px){
  .cart-promo{
    flex-wrap: wrap;
    &__title{
      width: 100%;
      margin: 0 0 .3em;
    }
    .fg{
      flex-grow: 1;
      margin: 0 1em 0 0;
    }
    .m-btn-gray-outline{
      padding: 0;
      width: 2.5em;
      height: 2.5em;
      flex-shrink: 0;
      svg{
        width: 1.5em;
        display: block;
      }
      span{
        display: none;
      }
      &:hover{
        path{
          stroke: $white;
        }
      }
    }
  }
}
