:root {
    --vh: 1vh;
}

$black: #282828;
$dark: #333;
$white: #fff;
$primary: #EE1C25;
$green: #0C861F;
$yellow: #F2C94C;
$gray: #4E4E54;
$textLightGray: #B2B2B2;
$textMediumGray: #808080;
$textDarkBlack: #333333;
$dividerGray: #D5D4D3;
$bgGray: #F1F1F1;
$shadow: 0 1em 1em 0 rgba(0, 0, 0, 0.10), 0 .5em .5em 0 rgba(0, 0, 0, 0.05);
$vh: calc(100 * var(--vh));

$border: 1px solid #E4E7EC;
$border2: 1px solid $dividerGray;
$border3: 1px solid $textLightGray;
