.return-req{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25em;
  }
  &__btn{
    padding: 0 2em;
    margin-left: 1em;
  }
}

._no-wrap{
  white-space: nowrap;
}

.images-slider{
  margin-top: 1.5em;
  width: 38em;
  max-width: 100%;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      opacity: 1;
      pointer-events: unset;
      width: calc(100% + 1em);
    }
    &-slide{
      width: 25%;
      padding-right: 1em;
    }
    &-button{
      transform: translateY(-50%);
      margin: 0;
      width: 1.5em;
      height: 1.5em;
      @include center;
      background-image: unset;
      &:after{
        display: none;
      }
      img{
        width: 100%;
      }
      &-prev{
        left: -2.25em;
      }
      &-next{
        right: -2.25em;
      }
    }
  }
}

.review-form{
  .rating{
    margin-top: .5em;
  }
  &__fields{
    margin-top: .5em;
    @include grid100gap(1em);
  }
  .fg{
    label{
      font-weight: 400;
    }
    textarea{
      height: 5em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__btn{
    margin-right: 1em;
    &:last-child{
      margin: 0;
    }
  }
}

.rating {
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.5em;
    margin-right: .5em;
    svg {
      width: 100%;
      height: auto;
    }
    path {
      fill: $textLightGray;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: $primary;
      }
      &~.rating-input__item {
        path {
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: $primary;
  }
}

@media screen and (max-width: 992px){
  .return-req{
    &__top{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: .625em 0 0;
    }
  }

  .images-slider{
    width: 100%;
    padding: 0 2.5em;
    .swiper-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .images-slider{
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: 1.25em;
      }
    }
  }
}
