.map-modal{
  .m-modal__dialog{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-width: 100%;
    padding: 1.25em 3em 3em;
    border-radius: 0.75em 0.75em 0 0;
    height: calc(100 * var(--vh) - 2.75em);
  }
  &__inner{
    height: calc(100 * var(--vh) - 9.75em);
    margin-top: .8125em;
    display: grid;
    grid-template-columns: calc(100% - 21.5em) 20em;
    gap: 1.5em;
  }
  &__col{
    height: 100%;
  }
  &__col2{
    height: 100%;
    position: relative;
  }
}

.map{
  height: 100%;
  .ymaps-2-1-79-balloon{
    font-size: 1em;
    font-family: "akrobat-regular", Helvetica, Arial, sans-serif;
    //box-shadow: none;
    padding: 0;
    //max-width: 15em;
    &__tail{
      box-shadow: none;
      background: unset;
      &:after{
        display: none;
      }
    }
    &__layout{
      box-shadow: 0 .25em 1.5em rgba(0, 0, 0, 0.25);
      border-radius: .625em;
      padding: 1.25em;
      position: relative;
    }
    &__content{
      box-sizing: border-box;
      font-family: "akrobat-regular", Helvetica, Arial, sans-serif;
      font-size: 1em;
      padding: 0;
      //min-width: 15em;
      margin-right: 0;
      &>*{
        width: 100% !important;
        height: unset !important;
      }
    }
    &__close{
      position: absolute;
      top: .25em;
      right: .25em;
      &-button{
        width: 1.875em;
        height: 2.125em;
      }
    }
  }
}

.place-card{
  width: 13em;
  &__logo{
    width: 5.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    margin: .75em 0 1.25em;
    @include imgCover(62%);
    border-radius: .5em;
  }
  &__content{
    @include grid100gap(1em);
    *{
      line-height: inherit;
    }
  }
  &__nav{
    display: flex;
    margin-top: .75em;
  }
}


@media screen and (max-width: 992px){
  .map-modal{
    .m-modal__dialog{
      height: calc(100 * var(--vh) - 2em);
      padding: 1.25em 1em 0;
      overflow: hidden;
    }
    .close-btn{
      top: 1.5em;
      right: 1.5em;
    }
    &__title{
      max-width: 14.25em;
      span{
        display: none;
      }
    }
    &__inner{
      height: calc(100 * var(--vh) - 9.875em);
      margin-top: .75em;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__col2{
      display: none;
    }
    &._show-list{
      .map-modal__col{
        display: none;
      }
      .map-modal__col2{
        display: block;
      }
    }
  }

  .map{
    height: calc(100 * var(--vh) - 9.875em);
  }
}


