.fg{
  textarea,
  input{
    width: 100%;
    height: 3.125em;
    border: 1px solid #DBDBDB;
    font-size: 1em;
    padding: 0 1.19em;
    &::placeholder{
      color: #CECECE;
    }
  }
  .nice-select {
    border: 2px solid #D0D5DD;
    width: 100%;
    background-color: transparent;
    color: $black;
  }
  .nice-select {
    font-size: 1em;
    height: 2.5em;
    padding: 0 3em 0 .875em;
    background-image: url(../img/select-arrow.svg) !important;
    background-repeat: no-repeat !important;
    background-position: right .875em center !important;
    background-size: 1.125em auto !important;
    .current {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      font-size: 1.125em;
    }
  }
  .list {
    //margin-top: 0;
    background: #fff;
    box-shadow: 0 1em 1em 0 rgba(0, 0, 0, 0.10), 0 .5em .5em 0 rgba(0, 0, 0, 0.05);
    border: none;
    padding: 1.5em 1em;
    @include grid100gap(1em);
    max-height: 18em;
    overflow-y: auto;
  }
  .option {
    border: none;
    background-color: transparent;
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.05625em;
    padding: 0 2em 0 0;
    &:hover {
      color: $primary;
    }
  }
  .option:hover,
  .option.focus,
  .option.selected.focus {
    background-color: transparent;
    font-weight: 400;
  }
  .option.selected {
    background-image: url(../img/check3.svg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 1em auto;
  }
  .date-input {
    padding-right: 2em;
    background-image: url(../img/calendar.svg);
    background-repeat: no-repeat;
    background-size: .89em auto;
    background-position: right .78em center;
  }
  &--type2{
    label{
      color: #344054;
      font-weight: 600;
      font-size: .875em;
      line-height: 143%;
      margin-bottom: .5em;
    }
    textarea,
    input{
      font-size: 1.125em;
      padding: 0 .78em;
      border-width: 2px;
      height: 2.22em;
      border-radius: .44em;
    }
    textarea{
      padding-top: .55em;
      padding-bottom: .55em;
      height: 6.67em;
      resize: none;
    }
  }
}

.m-switch,
.upload,
.m-radio,
.order-radio,
.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 3;
    cursor: pointer;
  }
}

.m-radio,
.m-check {
  input {
    &:checked~label {
      &::after {
        opacity: 1;
      }
    }
  }
  label {
    font-size: 1em;
    padding-left: 2em;
    position: relative;
    min-height: 1.5em;
    cursor: pointer;
    display: block;
    margin: 0;
    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 1.5em;
      height: 1.5em;
      left: 0;
      top: 0;
    }
    &:before {
      border-radius: .25em;
      border: 2px solid $dividerGray;
      transition: all .100s;
    }
    &:after {
      z-index: 2;
      background-image: url(../img/check.svg);
      @include bgImgSize(1.125em);
      opacity: 0;
    }
  }
  span {
    font-size: 0.875em;
    line-height: 172%;
    letter-spacing: 0.05625em;
  }
  a {
    color: $primary;
    position: relative;
    z-index: 3;
  }
  &._black {
    label{
      color: $black;
    }
    label:after {
      //background-image: url(../img/check4.svg);
    }
  }
}

.m-radio{
  input:checked{
    &~label{
      &:before{
        background-color: #f2f2f2;
      }
    }
  }
  label{
    &:before{
      border-radius: 50%;
    }
    &:after {
      background-image: url(../img/radio.svg);
      @include bgImgSize(.625em);
    }
  }
}


