.return-form{
  margin-top: 1.25em;
  padding: 0 .625em;
  &__item{
    display: grid;
    grid-template-columns: 17.5em 32em;
    gap: .5em 2em;
    align-items: flex-start;
    margin-top: 1.25em;
    &-col{
      padding-top: .75em;
      label{
        margin-bottom: 0;
        font-weight: 500;
      }
    }
    .nice-select .disabled{
      display: none;
    }
  }
  &__checks{
    @include grid100gap(.625em);
    .m-radio,
    .m-check{
      display: flex;
      align-items: center;
      min-height: 2.75em;
      label{
        font-weight: 400;
        margin: 0;
        &:after,
        &:before{
          top: -.125em;
        }
        span{
          font-size: 1.125em;
          line-height: 111%;
        }
      }
    }
  }
  .upload,
  .order-contacts{
    margin-top: 1.25em;
  }
  .order-bottom{
    &__hint{
      margin-top: 1.75em;
      font-size: 1.0625em;
      line-height: 167%;
    }
  }
}

.upload {
  input:hover~.upload__inner {
    border-color: $primary;
  }
  &__inner {
    transition: all .3s;
    padding: 1em 1.5em;
    border-radius: 0.5em;
    border: 1px solid #E4E7EC;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__icon {
    width: 2.5em;
    height: 2.5em;
    margin: .25em 0 .75em;
    @include center;
    border-radius: 50%;
    border: .375em solid #F9FAFB;
    background: #F2F4F7;
    img {
      width: 1.25em;
    }
  }
  .text14 {
    line-height: 143%;
    &:not(:first-child) {
      margin-top: .285em;
    }
  }
}

.uploaded{
  &__grid{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 2em);
  }
}

.uploaded-item,
.images-slider{
  &__img{
    @include imgCover(100%);
    border-radius: .25em;
    border: $border3;
  }
}

.uploaded-item{
  width: 8.75em;
  margin: 1em 2em 0 0;
  position: relative;
  &__delete{
    position: absolute;
    @include center;
    border-radius: 50%;
    background: #FFF;
    width: 2.25em;
    height: 2.25em;
    bottom: -1em;
    right: -.5em;
    svg{
      width: 1.25em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .return-form{
    padding: 0;
    &__item{
      grid-template-columns: 100%;
      &-col{
        padding-top: 0;
      }
    }
  }
}


