.order-page{
  margin: 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 28.75em) 26.25em;
    gap: 1.5em 2.5em;
    margin-top: .625em;
    position: relative;
  }
  &__col2{
    position: absolute;
    top: 0;
    right: 0;
    width: 26.25em;
    height: 100%;
  }
}

.order-table-head,
.order-row{
  display: grid;
  grid-template-columns: 4.375em 4em 10em 6em 4.625em 6.875em;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.25em;
  text-align: center;
  &__col{
    &:nth-child(3){
      text-align: left;
    }
  }
}

.order-table-head{
  margin-bottom: .625em;
  border-top: $border2;
  border-bottom: $border2;
  padding: .56em 1.25em;
  &__title{
    font-size: .9375em;
    line-height: 167%;
    letter-spacing: .06em;
  }
}

.order-row{
  margin-top: .625em;
  border-bottom: $border2;
  &__img{
    @include imgCover(100%);
  }
  &__mob-title{
    display: none;
  }
}

.order-city{
  margin-top: 3.25em;
  &__top{
    @include grid100gap(.625em);
  }
  &__select{
    margin-top: .625em;
    padding: 1.25em;
    border-radius: 0.5em;
    border: $border2;
  }
}

.place-selected{
  min-height: 4.375em;
  margin-top: .625em;
  padding: 1.25em;
  border-radius: 0.5em;
  border: $border2;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:first-child{
    margin-top: 0;
  }
  &__btn{
    margin-left: 1em;
    flex-shrink: 0;
  }
  &._center{
    justify-content: center;
    .place-selected__btn{
      margin: 0;
    }
  }
}

.hint-input{
  position: relative;
  .fg{
    input{
      font-size: 1.125em;
      padding: 0 .78em;
      border-width: 2px;
      height: 2.22em;
      border-radius: .44em;
    }
  }
  &._open{
    .hint-input__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__dropdown{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .75em;
    border-radius: 0.5em;
    background-color: $white;
    box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.10), 0px 4px 6px -2px rgba(16, 24, 40, 0.05);
    padding: .25em .5em .25em 0;
    z-index: 5;
  }
  &__options{
    max-height: 22em;
    overflow-y: auto;
    @include scroll;
  }
  &__option{
    padding: .625em 3.5em .625em .875em;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: background-color .3s;
    background-image: url(../img/check.svg);
    background-size: 0 auto;
    background-position: right 1.25em center;
    background-repeat: no-repeat;
    &:hover,
    &._selected{
      background-color: #F1F1F1;
    }
    &._selected{
      background-size: 1em auto;
    }
  }
}


@media screen and (max-width: 992px){
  .order-page{
    &__inner{
      grid-template-columns: 100%;
      margin-top: 1.25em;
    }
    &__col2{
      position: static;
      height: unset;
      width: 100%;
      margin-top: 1.5em;
    }
  }

  .order-table{
    font-size: .9375em;
  }

  .order-city{
    margin-top: 1.5em;
    &__desc{
      display: none;
    }
    &__select{
      padding: .75em .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-table{
    font-size: 1em;
  }

  .order-table-head{
    display: none;
  }

  .order-row{
    grid-template-columns: 100%;
    gap: .25em;
    text-align: left;
    margin: 0;
    padding: 1.25em 0 1em;
    &__col{
      display: grid;
      grid-template-columns: 7.5em calc(100% - 7.75em);
      gap: .25em;
      &--img{
        order: -1;
        display: block;
        width: 7.1875em;
        margin: 0 auto .75em;
      }
    }
    &__mob-title{
      display: block;
    }
    .text14,
    .text18{
      font-weight: 700;
    }
    &__mob-title,
    .text14,
    .text18{
      font-size: 1em;
      line-height: 125%;
    }
  }
}
