.lk{
  margin-top: 0;
  padding-bottom: 4em;
  &__items{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(4,14.375em);
    justify-content: space-between;
    gap: 7em 2em;
  }
}

.lk-link{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $primary;
  &__img{
    background: #F4F4F4;
    width: 9.375em;
    height: 9.375em;
    @include center;
    margin-bottom: 1.25em;
    border-radius: 50%;
    img{
      max-width: 6em;
      max-height: 6em;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 129%;
    font-weight: 700;
    letter-spacing: .064em;
  }
}


@media screen and (max-width: 992px){
  .lk{
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(4,9em);
      gap: 1em;
    }
  }

  .lk-link{
    &__img{
      width: 7.5em;
      height: 7.5em;
      margin-bottom: 0;
      img{
        max-width: 4.5em;
        max-height: 4.5em;
      }
    }
  }

  .breadcrumbs{
    &~.h1{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 576px){
  .lk{
    &__items{
      grid-template-columns: repeat(2,9em);
    }
  }
}
