.profile{
  margin: 0;
  &__inner{
    display: grid;
    grid-template-columns: 13.75em calc(100% - 17.5em);
    gap: 1em 3.75em;
    margin-top: 2em;
  }
  .contacts-persons__group{
    max-width: 100%;
  }
  .page-title{
    padding: 0;
  }
  .form-item{
    &__fields{
      max-width: 100%;
    }
    &__check{
      margin-bottom: .9375em;
    }
  }
}

.profile-nav{
  position: sticky;
  top: 6em;
  border-right: $border2;
  padding-right: 1em;
  &__mob{
    display: none;
  }
  &__items{
    @include grid100gap(1.25em);
  }
  &__top{
    display: flex;
  }
  &__link,
  &__sublink{
    &._active{
      color: $textLightGray;
    }
  }
  &__link{
    display: flex;
    font-size: 1.125em;
    line-height: 167%;
    font-weight: 700;
    letter-spacing: 0.05625em;
  }
  &__count{
    margin-left: .25em;
    border-radius: 0.375em;
    min-width: 0.75em;
    height: 0.75em;
    padding: 0 .0625em;
    background-color: $primary;
    color: #fff;
    font-weight: 800;
    @include center;
    span{
      font-size: .5em;
    }
  }
  &__bottom{
    padding: .5em 0 0 1.25em;
  }
  &__sublinks{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__sublink{
    @include lastM(0 0 .625em);
    font-size: 1em;
    line-height: 187%;
    font-weight: 700;
    letter-spacing: 0.05625em;
  }
}

.load-more{
  display: flex;
  justify-content: center;
  margin-top: 1.25em;
}


@media screen and (max-width: 992px){
  .profile{
    padding-bottom: 3em;
    overflow: hidden;
    &__inner{
      grid-template-columns: 100%;
    }
    .order__subtitle{
      padding: 0;
    }
  }

  .profile-nav{
    top: unset;
    position: relative;
    border: none;
    padding: 0;
    &._open{
      .profile-nav__items{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__mob{
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 2.75em;
      border-radius: 0.5rem;
      border: 2px solid #D0D5DD;
      box-shadow: 0 .0625em .125em rgba(16, 24, 40, 0.05);
      padding: 0 .875em;
      svg{
        width: 1.25em;
        height: auto;
        margin-left: 1em;
      }
    }
    &__items{
      gap: 1em;
      padding: 1.5em 1em;
      min-width: 13.3125em;
      background-color: #fff;
      box-shadow: 0 1em 1em rgba(0, 0, 0, 0.10), 0 .5em .5em rgba(0, 0, 0, 0.05);
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: .25em;
      z-index: 5;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
    &__link,
    &__sublink{
      font-size: 1em;
      line-height: 150%;
      font-weight: 500;
    }
    &__bottom{
      padding-top: 1em;
    }
    &__sublink{
      @include lastM(0 0 1em);
    }
  }
}


