.user-options__item_cart{
  &-count{
    position: absolute !important;
    height: 12px !important;
    width: auto !important;
    padding: 0 2px !important;
    min-width: 12px !important;
    @include center;
    background-color: $primary;
    border-radius: 50%;
    color: #fff !important;
    font-size: 8px;
    font-weight: 800;
    top: 10px;
    right: 10px;
  }
  &._added{
    path{
      stroke: $primary;
    }
  }
}
