.order-delivery{
  margin-top: 1.875em;
  &__grid{
    margin-top: .625em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
  }
}

.order-radio{
  input:checked~.order-radio__inner{
    border-color: $primary;
    outline: 1px solid $primary !important;
  }
  input:disabled{
    pointer-events: none;
    &~.order-radio__inner{
      opacity: 0.2;
    }
  }
  &__inner{
    height: 100%;
    display: flex;
    align-items: flex-start;
    border-radius: 0.5em;
    border: $border2;
    padding: 1em;
    transition: all .3s;
  }
  &__icon{
    width: 1.75em;
    margin-right: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.order-contacts{
  margin-top: 1.875em;
  &__top{
    @include grid100gap(.625em);
  }
  &__fields{
    margin-top: .625em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }
  &__name{
    grid-column: 1/-1;
    max-width: 31.25em;
  }
}

._full{
  grid-column: 1/-1;
}

.order-hint{
  padding: 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.5em;
  border: 1px solid #F4F4F4;
  background: #FFF;
  margin-top: 2.875em;
  box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.10), 0px 4px 6px -2px rgba(16, 24, 40, 0.05);
  &__btn{
    margin-left: .5em;
    span{
      font-size: 1.125em;
      line-height: 167%;
    }
  }
}

._gap{
  opacity: 0;
  margin: 0;
  display: block;
  height: 1.375em;
}

.order-bottom{
  margin-top: 2em;
  .m-check{
    margin-top: .875em;
    &:first-child{
      margin-top: 0;
    }
    label{
      color: $black;
      span{
        line-height: 143%;
      }
    }
  }
  &__btn{
    margin-top: 2.125em;
    height: 4.125em;
    @include center;
    border-radius: 0.5em;
    background: #F2F2F2;
  }
}


@media screen and (max-width: 992px){
  .order-delivery{
    margin-top: 1.25em;
  }

  .order-radio{
    &__inner{
      padding: 1em .5em;
      display: block;
    }
    &__icon{
      margin: 0 0 .5em;
      height: 1.75em;
      width: auto;
      img{
        height: 100%;
        width: auto;
      }
    }
  }

  .order-contacts{
    margin-top: 1.25em;
  }

  .order-bottom{
    margin-top: 1.5em;
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .order-delivery{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
    }
  }

  .order-contacts{
    &__fields{
      grid-template-columns: 100%;
    }
  }

  .order-hint{
    font-size: .875em;
  }
}
