@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.5em;
    border-radius: 0.25em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
        letter-spacing: .064em;
    }
}

.red-text-btn{
    font-weight: 700;
    color: $primary;
    &:hover{
        color: $black;
    }
    span{
        font-size: .875em;
        letter-spacing: .064em;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-red-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-gray-outline{
    border: 1px solid $textLightGray;
    color: $black;
    &:hover{
        background-color: $primary;
        border-color: $primary;
        color: $white;
    }
}
