.map-modal{
  &__items{
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    padding: 1em 1.25em .5em;
    overflow-y: auto;
    @include scroll;
    height: calc(100 * var(--vh) - 19.25em);
  }
  &__top{
    width: 100%;
    .fg{
      label{
        font-weight: 400;
      }
    }
  }
  &__switches{
    margin-top: 1em;
    padding: 0 1em;
    @include grid100gap(.5em);
  }
  &__more{
    width: 100%;
    margin-top: 1em;
  }
  &__tabs{
    display: none;
  }
}

.point-item{
  border-bottom: 1px solid #E6E6E6;
  padding: .5em 0;
  margin-bottom: .5em;
  @include grid100gap(.25em);
  position: relative;
  &._active,
  &:hover{
    cursor: pointer;
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    @include centerAbsolute;
    box-shadow: 0 .25em .5em .1875em rgba(0, 0, 0, 0.15), 0 .0625em .1875em 0 rgba(0, 0, 0, 0.30);
    height: 100%;
    width: calc(100% + 1em);
    background-color: $white;
    border-radius: .5em;
    z-index: -1;
    transition: all .3s;
    opacity: 0;
  }
  &__desc{
    color: #8B8B8B;
  }
}

.m-switch{
  display: flex;
  align-items: center;
  justify-content: space-between;
  label{
    border: 1px solid #79747E;
    border-radius: 1.5em;
    flex-shrink: 0;
    width: 2.75em;
    height: 1.5em;
    display: flex;
    align-items: center;
    margin-left: 1em;
    padding: 0 .25em;
    transition: all .3s;
    background-color: #F2F2F2;
    &:before{
      content: '';
      width: 1em;
      height: 1em;
      background: #79747E;
      border-radius: 50%;
      transition: all .3s;
    }
  }
  &__text{
    font-size: .875em;
    line-height: 171%;
  }
  input:checked{
    &~label{
      background-color: $primary;
      border-color: $primary;
      &:before{
        background-color: $white;
        transform: translateX(1.25em);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .map-modal{
    &__tabs{
      display: grid;
      grid-template-columns: 50% 50%;
      margin-top: .125em;
    }
    &__tab{
      border-radius: 0 0.25em 0.25em 0;
      border: 1px solid #808080;
      height: 2em;
      @include center;
      transition: all .3s;
      font-weight: 700;
      &:first-child{
        border-radius: .25em 0 0 .25em;
        border-right: none;
      }
      &._active{
        color: #B2B2B2;
        border-color: #B2B2B2;
      }
      span{
        font-size: .875em;
      }
    }
    &__items{
      height: calc(100 * var(--vh) - 20.375em);
      padding-bottom: 1.75em;
      width: calc(100% + 1em);
      margin-left: -.5em;
    }
  }

  .point-item{
    &:before{
      display: none;
    }
  }
}


