.order-info{
  padding: 1.125em;
  border-radius: 1.25em;
  border: $border2;
  position: sticky;
  top: 4.5em;
  &__props{
    padding: .75em 0 0 1.25em;
    @include grid100gap(.375em);
  }
  &__prop{
    display: grid;
    grid-template-columns: 8.75em calc(100% - 9.25em);
    gap: .5em;
    &-col{
      .text14{
        line-height: 171%;
        opacity: .8;
      }
    }
    .text20,
    .text16{
      display: inline-block;
    }
    .text16{
      letter-spacing: 0.05625em;
    }
  }
  &__bottom{
    margin-top: 1.5em;
    padding: .75em 1.25em;
    border-radius: 0.5rem;
    background:  #F2F2F2;
    *{
      display: inline-block;
    }
    &-title{
      font-size: 1.125em;
      line-height: 115%;
      font-weight: 700;
    }
    &-price{
      font-size: 1.5em;
      font-weight: 800;
      color: rgba($primary,.8);
      margin-left: .33em;
      margin-right: .075em;
    }
  }
}

.order-address{
  @media screen and (min-width: 577px){
    .order-contacts__fields{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    }
    ._col2{
      grid-column: span 2;
    }
    .m-check{
      margin-top: 2.125em;
    }
  }
}

.warning-message2{
  background-color: transparent;
  img{
    width: 1.5em;
    margin-right: .625em;
  }
  button{
    display: inline-block;
    text-decoration: underline;
  }
}

.order-success{
  padding: 1.75em 0 5em;
  @include grid100gap(2em);
  &__btn{
    padding: 0 2em;
  }
  &__desc{
    p{
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 992px){
  .order-success{
    padding: 1.5em 0 3.125em;
    &__btn{
      padding: 0 2em;
    }
    &__desc{
      @include grid100gap(1.5em);
    }
  }
}


@media screen and (max-width: 576px){
  .order-info{
    padding: 1.25em 1em;
    position: static;
    &__props{
      padding-left: 0;
    }
    &__prop{
      display: grid;
      grid-template-columns: 7em calc(100% - 7.5em);
      &-col{
        .text14{
          line-height: 128%;
        }
      }
    }
  }
}
