.lk-filter__toggle,
.order-detail__link{
  &-count{
    min-width: 1.5em;
    @include center;
    height: 1.5em;
    font-size: .5em;
    line-height: 120%;
    font-weight: 800;
    color: #fff;
    background-color: $primary;
    border-radius: 50%;
    margin-left: .5em;
    flex-shrink: 0;
  }
}

.lk-filter{
  margin: 1.25em 0 1.5em;
  &__top{
    display: flex;
  }
  .catalog__sort{
    order: unset;
    margin: 0;
    min-width: 10em;
    .nice-select{
      border-width: 1px;
      display: flex;
      align-items: center;
      height: 1.5em;
      &:before{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/filter-icon.svg);
        @include bgImgSize(100%);
        width: 0.75em;
        height: 0.625em;
        margin-right: .5em;
      }
      .list{
        min-width: 100%;
        width: auto;
      }
    }
  }
  &__refresh{
    margin-left: 1em;
    font-weight: 700;
    display: none;
    span{
      font-size: .875em;
      line-height: 153%;
    }
  }
  &__toggle{
    padding: 0 .94em;
    height: 1.5em;
    border: $border3;
    display: flex;
    align-items: center;
    margin-left: 1.875em;
    font-weight: 500;
    svg{
      width: 1em;
      margin-right: .25em;
    }
    &-title{
      font-size: .875em;
    }
    ._view2{
      display: none;
    }
    &-count{
      display: none;
    }
    &:hover{
      border-color: $primary;
    }
    &._open{
      background-color: $dividerGray;
      border-color: $dividerGray;
      color: $textMediumGray;
      path{
        stroke: $textMediumGray;
      }
    }
    &._selected{
      color: $primary;
      path{
        stroke: $primary;
      }
      .lk-filter__toggle-count{
        display: flex;
      }
      &~.lk-filter__refresh{
        display: flex;
      }
    }
  }
  &__bottom{
    display: none;
    padding: 1.25em;
    margin-top: .625em;
  }
}

.filter{
  &__grid{
    display: grid;
    grid-template-columns: 23.5625em 17.5em 13.75em;
    gap: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
    .m-btn{
      margin-right: 1em;
    }
  }
}

.filter-item{
  &__top{
    margin-bottom: .25em;
  }
  .fg{
    input,
    .nice-select{
      border-width: 1px;
    }
    &._right{
      input{
        text-align: right;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(50% - .4375em) .875em calc(50% - .4375em);
  }
  &__split{
    @include center;
  }
}

.catalog{
  &__sort{
    margin-left: 1.875em;
    min-width: 13.3125em;
    .nice-select{
      height: 1.8em;
      border-radius: 0;
      padding: 0 2.44em 0 .94em;
      background-image: url(../img/select-arrow2.svg) !important;
      background-size: 1.25em auto !important;
      .current{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .lk-filter{
    margin-top: .625em;
  }

  .filter{
    &__nav,
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__nav{
      display: grid;
      width: 50%;
      margin-left: auto;
      margin-right: auto;
      .m-btn{
        margin: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .lk-filter{
    &__top{
      justify-content: space-between;
    }
    .catalog__sort{
      margin-right: .25em;
    }
    &__toggle{
      margin-left: 0;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .filter{
    &__grid{
      grid-template-columns: 100%;
    }
    &__nav{
      width: 100%;
    }
  }
}
