.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.h0{
  font-size: 3em;
  line-height: 125%;
  letter-spacing: 0.01875em;
}

.h1{
  font-size: 34px;
  line-height: 150%;
  letter-spacing: 0.9px;
}

.text34{
  font-size: 2.125em;
  line-height: 150%;
  letter-spacing: 0.026em;
}

.text24 {
  font-size: 1.5em;
  line-height: 125%;
  letter-spacing: .05625em;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 167%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 167%;
}

.text14{
  font-size: .875em;
  line-height: 143%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.primary-text{
  color: $primary;
}

.gray-text80{
  color: rgba($gray,.8);
}

.black80-text{
  color: rgba($black,.8);
}


@media screen and (max-width: 992px){
  .h0{
    font-size: 2em;
  }

  .h1{
    font-size: 24px;
  }

  .text34{
    font-size: 1.5em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
