.orders{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3));
  margin-bottom: 2em;
  @media screen and (min-width: 993px){
    .order-card{
      &:nth-child(3n-1),
      &:nth-child(3n){
        border-left: none;
      }
      &:nth-child(3)~.order-card{
        border-top: none;
      }
    }
  }
}

.order-card{
  display: block;
  background: rgba(255, 255, 255, 0.05);
  border: $border2;
  padding: 3.125em 1em 1.875em;
  position: relative;
  &:hover{
    color: $black;
  }
  &__top{
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 1.06em;
  }
  &__badge{
    height: 2em;
    display: flex;
    align-items: center;
    padding: 0 .81em;
    background-color: $yellow;
    font-weight: 700;
    margin-left: -1px;
    span{
      font-size: 1.125em;
      line-height: 133%;
      letter-spacing: 0.05625em;
    }
    img{
      width: 1.5em;
      margin-left: .625em;
    }
    &._blue{
      background-color: #2D9CDB;
      color: #fff;
    }
    &._green{
      background-color: #219653;
      color: #fff;
    }
    &._gray{
      background-color: $dividerGray;
      color: $textMediumGray;
    }
  }
  &__notification{
    margin-top: .12em;
    width: 1.5em;
    position: relative;
    &._has-message{
      .order-card__notification-badge{
        display: flex;
      }
      path{
        fill: #2B2F33;
      }
    }
    svg{
      width: 100%;
    }
    &-badge{
      position: absolute;
      right: 0;
      bottom: 0;
      min-width: 0.75em;
      height: 0.75em;
      padding: 0 .125em;
      @include center;
      display: none;
      background-color: $primary;
      border-radius: 50%;
      color: #fff;
      span{
        font-size: .5em;
      }
    }
  }
  &__name{
    text-align: center;
  }
  &__images{
    display: flex;
    margin: 2em 0;
  }
  &__img{
    margin-right: .3125em;
    width: 4.375em;
    height: 4.375em;
    border-radius: 0.25em;
    border: $border3;
    @include center;
    &:last-child{
      margin-right: 0;
    }
    img{
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__text{
    @include grid100gap(.25em);
  }
  &__props{
    margin: .5em 0 0 auto;
    @include grid100gap(.5em);
  }
  &__prop{
    font-size: 0.875em;
    line-height: 128%;
    letter-spacing: 0.05625em;
  }
}



@media screen and (max-width: 992px){
  .orders{
    grid-template-columns: repeat(2,calc(50%));
    margin-bottom: .5em;
    @media screen and (min-width: 577px){
      .order-card{
        &:nth-child(2n-1),
        &:nth-child(2n){
          border-left: none;
        }
        &:nth-child(2)~.order-card{
          border-top: none;
        }
      }
    }
  }

  .orders{
    grid-template-columns: 100%;
    .order-card{
      &:nth-child(1)~.order-card{
        border-top: none;
      }
    }
  }
}


