.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.m-modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 56em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2em 3em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: .625em;
        right: .625em;
    }
    .auth{
        margin: 0;
    }
}

.close-btn{
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    svg{
        width: 100%;
    }
    &:hover{
        path{
            stroke: $primary;
        }
    }
}



@media screen and (max-width: 992px){
    .m-modal{
        &__dialog {
            padding: 2em 1.5em;
        }
        .auth__title{
            text-align: center;
            width: 100%;
        }
    }
}


@media screen and (max-width: 576px){
    .m-modal{
        &__dialog {

        }
    }
}
