.m-table{
  table{
    width: 100%;
    border-spacing: .3125em 0;
  }
  td,th{
    height: 2.75em;
    padding: 0 1.25em;
  }
  th{
    background-color: $bgGray;
    font-weight: 700;
    text-align: center;
    &:first-child{
      text-align: left;
    }
    span{
      font-size: 0.75em;
      line-height: 120%;
      letter-spacing: 0.05625em;
    }
  }
  td{
    color: rgba($black,.8);
    span{
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.05625em;
    }
  }
  &__mob-title{
    display: none;
  }
}

.return-table{
  &__title{
    display: none;
  }
  td{
    height: 5.625em;
  }
  tbody{
    tr{
      position: relative;
      &:after{
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: calc(100% - .3125em);
        height: 1px;
        background: #D5D4D3;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .m-table{
    &__mob-title{
      display: block;
      font-size: 1em;
      line-height: 125%;
      color: #808080;
      letter-spacing: 0.05625em;
    }
  }

  .return-table{
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(.5em)
    }
    tr{
      @include grid100gap(.5em);
      padding: 1em 0;
    }
    td{
      padding: 0;
      height: unset;
      display: grid;
      grid-template-columns: 7.5em calc(100% - 7.75em);
      gap: 1.5em .25em;
    }
    &__title{
      display: block;
      grid-column: 1/-1;
      font-size: 1.25em;
      font-weight: 700;
      letter-spacing: .045em;
      color: $primary;
    }
  }
}


